<template>
  <div>
    <van-nav-bar
      
      title="日常管理"
      @click-left="()=>{
          $router.go(-1)
         }"
      left-arrow
      class="backgroundNaviBar"
      z-index="999"
      :border="false"
    >
      <!-- <van-icon name="search" slot="right" /> -->
    </van-nav-bar>
    <div class="containerDailyContent">
      <div class="dailyControlImageNavigationFlex">
        <div
          class="dailyControlImageNavigationCenter border-radius"
          v-for="(item,index) in ImageListContentDaily"
          :key="index"
          @click="btnNavigationTo(item.name)"
          v-show="item.state"
          :style="width"
        >
          <div class="dailyControlImageNavigation">
            <img :src="item.image" alt />
          </div>
          <div class="contentDaily">
            <span>{{item.name}}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import DailyNav from "../../../static/js/homeNav/nav";
import menuRouter from "../../../static/js/navigation/index";
import Vue from "vue";
import { Toast } from "vant";
Vue.use(Toast);
export default {
  data() {
    return {
      ImageListContentDaily: [],
      showMenu: false
    };
  },
  async mounted() {
    var user = sessionStorage.getItem("user");
    this.ImageListContentDaily = await DailyNav.ImageListContentDaily(user);
    var arr = this.ImageListContentDaily.length;
    if (arr >= 5) {
      this.width = "width:30%";
    } else {
      this.width = "width:46%";
    }
  },
  methods: {
    btnNavigationTo(e) {
      switch (e) {
        case "更多":
          // this.adminMenu(e);
          break;
        case "更多.":
          // this.adminMenu(e);
          Toast("正在开发测试中，请稍后");
          break;
        default:
          this.$router.push(menuRouter.menuRouter(e));
          break;
      }
    },
    adminMenu(e) {
      this.showMenu = !this.showMenu;
      var user = sessionStorage.getItem("user");
      for (let index = 0; index < this.ImageListContentDaily.length; index++) {
        var name = this.ImageListContentDaily[index].name;
        if (user === "admin" && e === "更多") {
          if (
            name === "反馈列表" ||
            name === "用户审核" ||
            name === "车辆管理"
          ) {
            if (this.showMenu) {
              this.ImageListContentDaily[index].state = true;
            } else {
              this.ImageListContentDaily[index].state = false;
            }
          }
        } else if (
          (user === "station" || user === "operator") &&
          e === "更多"
        ) {
          switch (name) {
            case "更多":
              this.ImageListContentDaily[index].state = false;
              break;
            case "车辆管理":
              this.ImageListContentDaily[index].state = true;
              break;
          }
        }
      }
    }
  },
  beforeDestroy() {
    // this.showMenu = true;
    // this.adminMenu("更多");
  }
};
</script>
<style scoped>
.containerDailyContent {
  height: 100%;
  width: 100%;
  /* text-align: center; */
  display: flex;
  justify-content: center;
  padding-top: 10px;
  /* background: white; */
}
.dailyControlImageNavigationFlex {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  height: 100%;
  width: 100%;
  justify-content: space-evenly;
}
.dailyControlImageNavigationCenter {
  width: 43%;
  text-align: center;
  /* margin-top: 20px; */
  margin: 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
  align-content: center;
  padding: 20px 0;
  background: white;
}
.dailyControlImageNavigation img {
  width: 56px;
  height: auto;
}
.dailyControlImageNavigation {
  width: 100%;
}
.contentDaily {
  display: flex;
  flex-direction: column;

  align-items: center;
}
</style>


